.contact-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 4vw;
    width: 99vw;
    color: aliceblue;
}

.contact-container h1{
    font-size: 9vw;
    margin-bottom: 1vw;
    margin: 0;
}

.contact-container h2{
    font-size: 4vw;
    margin-bottom: 1vw;
    color: rgb(160, 255, 0);
    margin: 0;
    margin-bottom: 2vw;
}

.contact-container .contact-form{
    display: flex;
    flex-direction: column;
    width: 30vw;
}

.contact-container .contact-form label{
    font-size: 1.5vw;
    margin-bottom: 0.2vw;
}

.contact-container .contact-form input{
    margin-bottom: 1.5vw;
    border: 0.1vw solid rgba(166, 255, 0, 0.555);
    border-bottom: 0.2vw solid rgba(166, 255, 0, 0.664);
    border-right: 0.2vw solid rgba(166, 255, 0, 0.589);
    border-radius: 0.5vw;
    color: rgb(255, 255, 255);
    padding: 0.6vw;
    background-color: rgba(1, 3, 32, 0.842);
}

.contact-container .contact-form textarea{
    margin-bottom: 1.5vw;
    border: 0.1vw solid rgba(166, 255, 0, 0.555);
    border-bottom: 0.2vw solid rgba(166, 255, 0, 0.664);
    border-right: 0.2vw solid rgba(166, 255, 0, 0.589);
    border-radius: 0.5vw;
    padding: 0.6;
    height: 10vw;
    color: aliceblue;
    background-color: rgba(1, 3, 32, 0.842);
    resize: none;
}

.contact-container .contact-form textarea::-webkit-scrollbar {
    width: 1vw;
}

.contact-container .contact-form textarea::-webkit-scrollbar-track {
    background: #0d1117; /* Dark background */
}

.contact-container .contact-form textarea::-webkit-scrollbar-thumb {
    background-color: rgba(166, 255, 0, 0.5); /* Blue color for the thumb */
}

.contact-container .contact-form textarea::-webkit-scrollbar-thumb:hover {
    background-color: rgba(166, 255, 0, 0.65); /* Darker blue on hover */
}

.contact-container .contact-form textarea::-webkit-scrollbar-button{
    display: none;
}

.contact-container .contact-form button{
    background-color: rgba(166, 255, 0, 0.8);
    border-radius: 0.5vw;
    height: 3vw;
    font-size: 1.7vw;
    font-weight: bold;
    color: rgb(255, 255, 255);
    border-bottom: 0.3vw solid rgba(69, 100, 12, 0.8);
    border-left: 0.3vw solid rgba(69, 100, 12, 0.3);
    border-right: 0.3vw solid rgba(69, 100, 12, 0.8);
    border-top: 0.3vw solid rgba(69, 100, 12, 0.3);
}

.contact-container .contact-form button:hover{
    background-color: rgba(167, 247, 20, 0.9);
    color: rgb(4, 131, 15);
}